import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useForm } from 'react-hook-form';
import ReCAPTCHA from 'react-google-recaptcha';

// Components
import { Button } from '..';
import { StyledContactForm } from './styles';

export const ContactForm = ({ contact, ...props }) => {
  const { register, errors, handleSubmit } = useForm();
  const [captcha, setCaptcha] = useState('');
  const [submitted, setSubmitted] = useState(false);
  const [apiError, setApiError] = useState(false);
  const [progress, setProgress] = useState(false);

  const submit = (data) => {
    if (captcha.length > 1) {
      setProgress(true);
      fetch('https://epk-api.herokuapp.com/api/aristo/email', {
        headers: { 'Content-Type': 'application/json; charset=utf-8' },
        method: 'POST',
        body: JSON.stringify(data),
      }).then((response) => {
        setProgress(false);
        if (response.status === 200) {
          // Success
          setSubmitted(true);
          setApiError(false);
        } else {
          // Fail
          setSubmitted(false);
          setApiError(true);
        }
      });
    }
  };

  // Wake API
  useEffect(() => {
    fetch('https://epk-api.herokuapp.com');
  }, []);

  return (
    <StyledContactForm {...props}>
      {!submitted && (
        <form onSubmit={handleSubmit(submit)}>
          <h2>Send us an inquiry</h2>
          <p>
            We will get back to as soon as possible. For immediate support, call
            us at {contact.phone}
          </p>
          <p>All fields are required.</p>
          <input
            type="text"
            name="name"
            ref={register({ required: true })}
            placeholder="Your full name"
          />
          {errors.name && 'Name is required.'}
          <input
            type="email"
            name="email"
            ref={register({
              required: true,
              pattern: /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
            })}
            placeholder="Email Address"
          />
          {errors.email && 'Email address is required.'}
          <input
            type="text"
            name="phone"
            ref={register}
            placeholder="Phone number"
          />
          <input
            type="text"
            name="message"
            ref={register({ required: true })}
            placeholder="Your message"
          />
          {errors.message && 'Message is required.'}
          <p>Please do not include confidential information in this message.</p>
          <ReCAPTCHA
            sitekey="6LfCsf0ZAAAAAPnbzct3G4iw-hXP7bSH1cf_r3Ue"
            onChange={(value) => setCaptcha(value)}
          />

          {!progress && <Button type="submit">Send Message</Button>}
          {progress && <h2>Loading</h2>}
          {apiError && <p>Something went wrong. Please try again.</p>}

          <p>
            Aristo values and protects your privacy. We do not give your
            personal information, without your consent, to any organization
            outside of our affiliates. Providing your phone number and email
            address enables Aristo to follow-up with you in regards to this
            request, we will not use your phone number or email for any other
            purpose.
          </p>
        </form>
      )}

      {submitted && (
        <div>
          <h2>Thanks for the message!</h2>
          <p>
            One of our representatives will reach out to you as soon as
            possible. For immediate support, call us at {contact.phone}.
          </p>
        </div>
      )}
    </StyledContactForm>
  );
};

ContactForm.propTypes = {
  contact: PropTypes.object.isRequired,
};
