import React from 'react';
import AniLink from 'gatsby-plugin-transition-link/AniLink';

// Const & Utils
import { useWindowSize } from '../../utils/useWindowSize';
import { mediaSize, colors } from '../../constants/theme';

// Components
import { Image } from '../../components';

export const Link = ({ children, ...props }) => {
  const logoPath = Image({ src: 'aristo-icon-light.png', linkOnly: true });
  const isMobile = useWindowSize().width <= mediaSize.condensedNav;
  const mobileBg = `url(${logoPath.src}) center/100px no-repeat ${colors.darkBg}`;
  const desktopBg = `url(${logoPath.src}) center no-repeat ${colors.brand}`;
  return (
    <AniLink
      cover
      direction="left"
      duration={1}
      bg={isMobile ? mobileBg : desktopBg}
      {...props}>
      {children}
    </AniLink>
  );
};
